.board_{
  width: 600px!important;
  height: 660px!important;
  margin-left: 5%;
  margin-top: 3%;
  border: 5px solid black;
}
body{
  background-color: #191919;
}
.name_space{
  color: white;
  margin: 5px;
  font-family: 'cascadia code';
}
.your_colour{
  color: aqua;
  font-size: large;
  font-family: 'Cascadia Code';
  position: absolute;
  left: 50%;
  top: 20%;
}
.game_status{
  color: aqua;
  font-size: xx-large;
  font-family: 'Cascadia Code';
  position: absolute;
  left: 50%;
  top: 15%;
}
.btn {
  background-color: aqua;
  color: #191919;
  font-family: 'Cascadia Code';
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background-color 0.2s ease, box-shadow 0.2s ease, outline-color 0.2s ease;
  position: absolute;
  left: 50%;
  top: 25%;
}

.btn:hover {
  background-color: #00b3b3;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

.btn:active {
  background-color: #005555;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  outline: 2px solid rgba(0, 0, 0, 0.6);
}
#name_bg{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  top: 0;
  z-index: 5;
}
#name_text {
  margin-left: 40%;
  font-family: 'Cascadia code';
  font-size: x-large;
  margin-top: 15%;
  color: #fff;
}
#name_input {
  margin-left: 7%;
  margin-top: 2%;
  width: 85%;
  height: 3%;
  padding-left: 5px;
  border-radius: 12px;
}
#name_button {
  font-size: xx-large;
  position: absolute;
  top: 47%;
  left: 45%;
}
.link_text{
  color: white;
  font-family: 'Cascadia code';
  font-size: large;
  text-decoration: white 3px underline ;
  padding-top: 8px;
  padding-left: 5px;
  margin-top: 1%;
}
.link_div{
  display: flex;
  width: 86%;
  background-color: grey;
  border-radius: 12px;
  margin-left: 7%;
  height: 7%;
  justify-content: space-between;
  margin-top: -20%;
  opacity: 0;
}
.copy_image{
  width: 45px;
  height: 45px;
  border-radius: 8px;
  margin-top: 0.3%;
  padding-right: 5px;
}